* {
  box-sizing: border-box;
}

body {
  max-height: 90vh;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

header {
  display: flex;
  width: 50%;
  margin: auto;
  margin-top: 40px;
}

header > * {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* ------------------DASHBOARD--------------------- */

.container {
  max-width: 70%;
  margin: auto;
  /* border: 1px solid #111; */
}

.upload-container {
  display: flex;
  justify-content: space-around;
  /* border-top: 2px solid #777; */
  padding-top: 8px;
}

.upload {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* border: 1px solid #111; */
  width: 50%;
  padding: 8px;
}

.upload > * {
  margin: 8px;
}

.image {
  margin-top: 40px;
  border-top: 2px solid #777;
  padding: 10px;
}

img {
  max-height: 500px;
  max-width: 100%;
}





/* ------------------GAME--------------------- */
.game {
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #555;
  width: 50%;
  /* height: 50%; */
  margin: auto;
}

.square {
  border: 1px solid #555;
  width: 25%;
  height: 25%;
}

.square:hover:not(.empty-square) {
  cursor: grab;
  background-color: rgba(153, 153, 153, 0.822);
}

.empty-square {
  background-color: #999;
  color: #999;
}

.even-square {
  background-color: #f4f4f4;
}


/* ------------------Game Controls--------------------- */
.game-controls {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 30%;
  margin: 20px auto;
}

button {
  padding: 8px 30px;
  border: none;
  margin: 5px 20px;
  border-radius: 5px;
  background-color: #d1d1d1;
  color: #444;
  font-size: 1.2rem;
}

button:hover {
  cursor: pointer;
}


@media (max-width: 760px) {
  header {
    width: 80%;
  }

  .game {
    width: 80%;
    /* height: 50%; */
    margin: auto;
  }

  .game-controls {
    width: 100%;
  }
}